.accordion {
  border: 1px solid transparent;
  border-bottom: 1px solid #3c3c3c;
  border-radius: 2px;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease-in-out, height 0.3s ease;
}
.accordion:last-child {
  margin-bottom: 0;
}
.accordion.show .accordion-body {
  display: block;
}
.accordion.accordion-open {
  background-color: #ffffff;
}
.accordion.accordion-open:first-child {
  border-top: 1px solid #3c3c3c;
}
.accordion.accordion-open .accordion-header:before {
  transform: rotate(180deg);
}
.accordion .accordion-header {
  text-decoration: none;
  touch-action: manipulation;
  cursor: pointer;
  line-height: 1rem;
  border: 1px solid transparent;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  user-select: none;
  color: #2da944;
  background-color: #ffffff;
  padding: 1rem;
  text-align: start;
  font-weight: 700;
  font-size: 1rem;
}
.accordion .accordion-header:before {
  color: #3c3c3c;
  margin: 0 0.5rem;
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 1.25rem;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: "liga";
  content: "keyboard_arrow_down";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
}
.accordion:hover .accordion-header {
  text-decoration: underline;
}
.accordion .accordion-body {
  padding: 0 1rem;
  display: none;
}
.accordion .expanding {
  height: 0;
  overflow: hidden;
  transition: height 0.3s ease;
}
