.nav {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 1rem 0;

    > ul {
        display: flex;
        padding: 0;
        margin: 0;
        list-style: none;
        margin-left: -0.75rem;
        margin-top: -0.25rem;

        li {
            a {
                border-bottom: none;
                display: inline-flex;
                text-decoration: none;
                padding: 0.75rem;

                span {
                    display: none;
                }

                &.active {
                    > span {
                        position: relative;

                        &:before {
                            content: "";
                            position: absolute;
                            background-color: @brand-primary;
                            height: 4px;
                            bottom: -2px;
                            left: 0;
                            right: 0;
                        }
                    }
                }
            }

            &.responsive-hidden {
                display: none;
            }

            span {
                color: dark-brown;
            }

            &:last-child {
                margin-right: 0;

                ul {
                    margin-bottom: 0;
                }
            }

            ul {
                list-style: none;
                padding: 0;
                margin: 0 0 0.5rem 3rem;

                li {
                    margin: 0;

                    .submenu-toggle {
                        display: none;
                    }

                    a {
                        color: @brand-secondary-dark-2;
                        font-size: @nav-submenu-font-size;

                        &:hover {
                            color: darken(@brand-secondary-dark-2, 10%);
                        }
                    }

                    &.active {
                        span {
                            position: relative;

                            &:before {
                                content: "";
                                position: absolute;
                                background-color: @brand-primary;
                                height: 4px;
                                bottom: -2px;
                                left: 0;
                                right: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    > a {
        border-bottom: none;
        line-height: 0.7;

        &.nav-openbtn {
            margin-top: @base-margin-sm;

            &:hover {
                cursor: pointer;
            }
        }
    }

    .material-icons {
        color: @brand-secondary;
        user-select: none;
    }
}

.nav-vertical () {
    ul {
        flex-direction: column;

        li {
            position: relative;
            margin: 0;

            &.active,
            a.active {
                &:before {
                    content: "";
                    position: absolute;
                    background-color: @brand-primary;
                    width: 4px;
                    height: 100%;
                    bottom: -2px;
                    top: 0;
                    left: 0;
                }
            }

            a.active {
                background-color: @light-gray;
                border-radius: 2px;
            }

            a {
                padding-left: calc(0.75rem + 4px);
            }

            &.responsive-hidden {
                display: list-item;

                .submenu {
                    ul {
                        margin-top: 0;
                        display: none;
                    }
                }
            }

            .submenu {
                > a {
                    display: none;

                    &.submenu-toggle {
                        display: list-item;
                        cursor: pointer;
                    }
                }

                &.submenu-open {
                    &.submenu-active-parent {
                        &:before {
                            content: none;
                        }
                    }

                    ul {
                        margin: 0;
                        z-index: @zindex-nav-submenu;
                        background-color: @white;
                        display: block;
                        position: absolute;
                        left: 4rem;
                        top: 0.5rem;
                        box-shadow: 1px 3px 5px 0 rgba(0, 0, 0, 0.25);
                        white-space: nowrap;

                        li {
                            a {
                                display: flex;
                                font-size: @base-font-size;
                                color: @brand-secondary;
                                padding: 0.5rem 1rem 0.5rem 0.75rem;

                                span {
                                    display: list-item;
                                }
                            }


                            &.active {
                                span:before {
                                    content: none;
                                }
                            }

                            &:hover {
                                text-decoration: none;
                                background-color: @light-gray;
                            }

                            &:not(:last-child) {
                                a {
                                    border-bottom: 1px solid @light-gray;
                                }
                            }
                        }
                    }
                }

                &.submenu-active-parent {
                    position: relative;

                    &:before {
                        content: "";
                        position: absolute;
                        background-color: @brand-primary;
                        width: 4px;
                        height: 100%;
                        bottom: -2px;
                        top: 0;
                        left: 0;
                    }

                    background-color: @light-gray;
                    border-radius: 2px;
                }
            }

            i {
                margin-right: 0;
                font-size: @nav-submenu-icon-fontsize-vertical;
                line-height: @nav-icon-lineheight;
            }
        }
    }

    .nav-openbtn {
        display: none;
    }
}

.nav-vertical-wide () {
    .nav-open();

    .nav-openbtn {
        display: none;
    }

    ul {
        li {
            &:last-child {
                .submenu {
                    ul {
                        margin-bottom: 0;
                    }
                }
            }

            a.active {
                &:before {
                    content: none;
                }
            }

            &.responsive-hidden {
                display: list-item;

                .submenu {
                    span {
                        display: inline;
                    }

                    ul {
                        display: block;
                    }
                }
            }

            a {
                padding: 0.5rem;

                span {
                    display: inline;
                }
            }

            i {
                padding: 0;
            }

            .submenu {
                > a {
                    display: flex;
                }

                &.submenu-toggle {
                    display: none;
                }
            }
        }
    }
}

.nav-open {
    > ul {
        flex-direction: column;
        margin: 0;
        margin-left: -0.5rem;

        > :first-child {
            margin-top: 0;
        }

        li {
            a,
            span {
                span {
                    display: inline;
                    color: @brand-secondary-dark-2;

                    &:hover {
                        color: darken(@brand-secondary-dark-2, 8%);
                    }
                }
            }

            a.active {
                background-color: transparent;
            }

            a {
                padding: 0.5rem;
            }

            &.active:before {
                content: none;
            }

            div {
                &.submenu {
                    &:hover {
                        cursor: pointer;
                    }

                    > a {
                        color: @brand-secondary-dark-2;
                        margin-bottom: -0.5rem;
                    }

                    .submenu-toggle {
                        display: none;
                    }

                    > ul {
                        a {
                            padding: 0.5rem;
                        }
                    }

                    &.submenu-active-parent {
                        background-color: transparent;

                        &:before {
                            content: none;
                        }
                    }
                }
            }
        }

        .material-icons {
            margin-right: @base-margin;
            font-size: 24px;
        }
    }
}

each(@grid-breakpoints, .(@min-width, @infix) {
    .nav-@{infix}-vertical {
        @media screen and (min-width: @min-width) {
            .nav-vertical();
        }
    }

    .nav-@{infix}-vertical-wide {
        @media screen and (min-width: @min-width) {
            .nav-vertical-wide();
        }
    }
});
